
import React, { useState } from 'react';


export default function TextForm(props) {
    const handleUpClick = () => {
        // console.log("hello" + text);
        let newText = text.toUpperCase();
        setText(newText);
        props.showAlert(" Converted to Uppercase","success");

    }
    const handleDownClick = () => {
        // console.log("down click"+text);
        let newText1 = text.toLowerCase();
        setText(newText1);
        props.showAlert(" Converted to Lowercase","success");
    }
    const handleClick = () => {
        // console.log("down click"+text);
        let newText2 = " ";
        setText(newText2);
        props.showAlert(" Cleared it","success");
    }
    const handleOnChange = (event) => {
        // console.log("Rashi");
        setText(event.target.value);
    }
    const handleCopy = () => {
        var text = document.getElementById("myBox");
        text.select();
        navigator.clipboard.writeText(text.value);
        props.showAlert("Copied!","success");
    }

    const handleSpace = () => {
        let newTex1 = text.split(/[ ]+/);
        setText(newTex1.join(" "));
        props.showAlert("All the spaces has been removed","success");
    }
    const [text, setText] = useState("");
    return (
        <>
            <div className="container" style={{ color: props.mode === 'dark' ? 'white' : 'black' }}>
                <h1>{props.heading}</h1>
                <div className="mb-3">
                    <textarea className="form-control" value={text} onChange={handleOnChange} style={{ backgroundColor: props.mode === 'dark' ? 'grey' : 'white', color: props.mode === 'dark' ? 'white' : 'black' }} id="myBox" rows="8"></textarea>
                </div>
                <button className="btn btn-primary mx-2" onClick={handleUpClick} >Convert to UpperCase</button>
                <button className="btn btn-primary mx-2" onClick={handleDownClick} >Convert to LowerCase</button>
                <button className="btn btn-primary mx-2" onClick={handleClick} >Clear</button>
                <button className="btn btn-primary mx-2" onClick={handleCopy} >Copy</button>
                <button className="btn btn-primary mx-2" onClick={handleSpace} >Extra Space</button>
            </div>
            <div className="container my-3" style={{ color: props.mode === 'dark' ? 'white' : 'black' }}>
                <h2>Your text Summary</h2>
                <p>{text.split(" ").length-1} words, {text.length} characters</p>
                <p>{0.008 * text.split(" ").length}Minutes to read</p>
                <h2>Preview</h2>
                <p>{text.length>0?text:"Enter something in text box for the preview"}</p>
            </div>
        </>
    )
}
